import request from '@/utils/request'

// 获取个人信息
export function getUser(username) {
    return request({
        url: '/blog/user/' + username,
        method: 'get',
    })
}

//获取用户卡余额
export function getCardMsg(username) {
    return request({
        url: '/blog/user/card/' + username,
        method: 'get',
    })
}

//获取每小时收费
export function getCostHour() {
    return request({
        url: '/blog/user/cost/',
        method: 'get',
    })
}

// 修改用户信息
export function updateUser(user) {
    return request({
        url: '/blog/user',
        method: 'put',
        data: user   // json格式提交
    })
}

// 修改密码
export function changePwd(info) {
    return request({
        url: '/blog/user',
        method: 'post',
        data: info   // json格式提交
    })
}