import Vue from 'vue'
import VueRouter from 'vue-router'

import { getToken } from "@/utils/auth";
import Register from "@/views/Register"
import Login from "@/views/Login"
import Member from "@/views/member"
import MemberArticle from "@/views/member/Article"
import CarEditor from "@/views/member/CarEditor"
import User from "@/views/member/User"
import ChangePwd from "@/views/member/ChangePwd"
import CardMsg from "@/views/member/CardMsg"
import CostHour from "@/views/member/CostHour"
import ParkMsg from "@/views/member/ParkMsg"
import Record from "@/views/member/Record"
import AdminLogin from "@/views/Login_admin"
import Member_admin from "@/views/member_ad"
import AdminMsg from "@/views/member_ad/AdminMsg"
import UserMsg from "@/views/member_ad/UserMsg"

Vue.use(VueRouter)

// 子路由的path不能有"/"
const routes = [
    {
        path: '',
        redirect: Login,
    },
    {
        path: '/login',
        name: 'Login',
        component: Login
    },
    {
        path: '/register',
        name: 'Register',
        component: Register
    },
    {
        path: '/member',
        name: 'Member',
        component: Member,
        redirect: '/member/article',
        children: [
            {
                path: 'article',
                name: 'MemberArticle',
                component: MemberArticle,
            },
            {
                path: '/member/carEditor/:carId',
                name: 'CarEditor',
                component: CarEditor,
                props: true
            },
            {
                path: 'user',
                name: 'User',
                component: User,
            },
            {
                path: 'changePwd',
                name: 'ChangePwd',
                component: ChangePwd,
            },
            {
                path: 'cardmsg',
                name: 'CardMsg',
                component: CardMsg,
            },
            {
                path: 'costHour',
                name: 'CostHour',
                component: CostHour,
            },
            {
                path: 'parkMsg',
                name: 'ParkMsg',
                component: ParkMsg,
            },
            {
                path: 'record',
                name: 'Record',
                component: Record,
            },
        ]
    },

    {
        path: '/login/admins',
        name: 'AdminLogin',
        component: AdminLogin
    },
    {
        path: '/admin',
        name: 'Member_ad',
        component: Member_admin,
        redirect: '/admin/adminMsg',
        children: [
            {
                path: 'adminMsg',
                name: 'AdminMsg',
                component: AdminMsg,
            },
            {
                path: 'userMsg',
                name: 'UserMsg',
                component: UserMsg,
            },
        ]
    }

]

const router = new VueRouter({
    // mode: "history",
    // base: "/blog_client",
    routes
})


router.beforeEach((to, from, next) => {// 路由守卫
    console.log(to.path)
    if (to.matched) {
        if (to.path.startsWith('/member') && !getToken())
            next({ path: '/login', query: { fromPath: to.path } })
        else next()
    } else {
        next()// 跳到404组件
    }
})

export default router
