<script>
import { getCostHour } from "@/api/blog/user";
export default {
  data: () => ({
    cost: null,
  }),
  created() {
    this.getUser();
  },

  methods: {
    getUser() {
      getCostHour().then((result) => {
        console.log(result.data.costHour);
        this.cost = result.data.costHour + "元/小时";
      });
    },
  },
};
</script>


