import request from '@/utils/request'

// 分页查询文章列表
export function listArticle(articleQuery) {
    return request({
        url: '/blog/car/listPage',
        method: 'get',
        params: articleQuery
    })
}

// 分页查询会员文章列表
export function listPark(parkMsg) {
    return request({
        url: '/blog/park/listMemberPage',
        method: 'get',
        params: parkMsg
    })
}

// 获取文章详细信息
export function getArticleDetail(carId) {
    return request({
        url: '/blog/car/' + carId,
        method: 'get',
    })
}

// 添加文章
export function addArticle(car) {
    return request({
        url: '/blog/car',
        method: 'post',
        data: car   // json格式提交
    })
}

// 修改预约位
export function updateCarN(parkMsg) {
    return request({
        url: '/blog/park',
        method: 'put',
        data: parkMsg   // json格式提交
    })
}

// 删除文章
export function deleteArticle(carIds) {
    return request({
        url: '/blog/car/' + carIds,
        method: 'delete',
    })
}