import request from '@/utils/request'

// 分页查询文章列表
export function listArticle(articleQuery) {
    return request({
        url: '/blog/car/listPage',
        method: 'get',
        params: articleQuery
    })
}

// 分页查询会员文章列表
export function listRecord(recordMsg) {
    return request({
        url: '/blog/record/listMemberPage',
        method: 'get',
        params: recordMsg
    })
}