import request from '@/utils/request'

// 分页查询文章列表
export function listArticle(articleQuery) {
    return request({
        url: '/blog/car/listPage',
        method: 'get',
        params: articleQuery
    })
}

// 分页查询会员文章列表
export function listMemberArticle(articleQuery) {
    return request({
        url: '/blog/car/listMemberPage',
        method: 'get',
        params: articleQuery
    })
}

// 获取文章详细信息
export function getArticleDetail(carId) {
    return request({
        url: '/blog/car/' + carId,
        method: 'get',
    })
}

// 添加文章
export function addArticle(file) {
    return request({
        url: '/blog/car',
        method: 'post',
        data: file   // json格式提交
    })
}

// 修改文章
export function updateArticle(file) {
    return request({
        url: '/blog/car/update',
        method: 'put',
        data: file,      // json格式提交
    })
}

// 删除文章
export function deleteArticle(carIds) {
    return request({
        url: '/blog/car/' + carIds,
        method: 'delete',
    })
}